import yaoqBar from '../../components/yaoqBar.vue';
export default {
	data() {
		return {
			data: {
				phone: "12314567910",
			},
			oshouc: false
		}
	},
	onLoad() {
		
	},
	methods: {
		shouc(){
			this.oshouc = !this.oshouc;
			if(this.oshouc){
				uni.showModal({
					title: '提示',
					content: '收藏成功！',
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '取消收藏成功！',
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		}
	},
	components: {
		yaoqBar
	},
};
